.modal {
   /* Hidden by default */
    display: none;
    position: fixed; /* Stay in place */
    z-index: 1; /* Sit on top */
    left: 0;
    top: 0;
    width: 100%; /* Full width */
    height: 100%; /* Full height */
    overflow: auto; /* Enable scroll if needed */
    background-color: rgb(0,0,0); /* Fallback color */
    background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
    -webkit-animation-name: fadeIn; /* Fade in the background */
    -webkit-animation-duration: 0.4s;
    animation-name: fadeIn;
    animation-duration: 0.4s
  }
  
  /* Modal Content */
  .modal-content {
    position: fixed;
    bottom: 10;
    background-color: gainsboro;
    width: 100%;

  }
.profileHeader {
  
  position: relative;
  height: 200px;
}
#myModal .backgroundImage {
  position: absolute;
  top: 0;
  left: 0;
  filter: blur(10px);
  z-index: 5;
  margin: 0 auto;
  }

  #myModal .profileImage{
    bottom: 0;
    position: absolute;
    align-content: center;
    margin-left: 45%;
    border-radius: 40%;
    z-index: 20;
    position: absolute;
  }



  /* The Close Button */
  .close {
    position: absolute;
    z-index: 30;
    color: white;
    float: right;
    font-size: 28px;
    font-weight: bold;
  }
  
  .close:hover,
  .close:focus {
    color: #000;
    text-decoration: none;
    cursor: pointer;
  }
  
  .modal-header {
    padding: 2px 16px;
    background-color: gainsboro;
    color: white;
  
    box-shadow: 10px 10px 5px grey;
  }
  

  /* Add Animation */
  @-webkit-keyframes slideIn {
    from {bottom: -300px; opacity: 0} 
    to {bottom: 0; opacity: 1}
  }
  
  @keyframes slideIn {
    from {bottom: -300px; opacity: 0}
    to {bottom: 0; opacity: 1}
  }
  
  @-webkit-keyframes fadeIn {
    from {opacity: 0} 
    to {opacity: 1}
  }
  
  @keyframes fadeIn {
    from {opacity: 0} 
    to {opacity: 1}
  }